import browser from "trix/config/browser"
import { makeElement, removeNode } from "trix/core/helpers/dom"

const sudamedia = {
  level2Enabled: true,

  getLevel() {
    if (this.level2Enabled && browser.supportsInputEvents) {
      return 2
    } else {
      return 0
    }
  },
  pickMedias(callback) {
    const button = makeElement("button", { type: "button", class: "trix-suda-media", id: this.fileInputId })

    button.addEventListener("click", () => {
      callback()
      // alert(123)
    })

    removeNode(document.getElementById(this.fileInputId))
    document.body.appendChild(button)
    button.click()
  }
}

export default sudamedia
